.form {
  grid-area: content;
  max-width: 430px;
  border-radius: 10px;
  background-color: #fff;
  display: flex;
  flex-direction: column;
}

@media screen and (max-width: 544px) {
  .form {
    max-width: 282px;
  }
}

.form__title {
  margin: 0;
  padding: 34px 64px 54px 36px;
  color: #000;
  font-weight: 900;
  font-size: 24px;
}

.form__title-confirm-delete {
  padding-bottom: 20px;
}

@media screen and (max-width: 544px) {
  .form__title {
    padding: 25px 22px 76px 22px;
    font-size: 18px;
  }

  .form__title-confirm-delete {
    padding-bottom: 10px;
  }
}

.form__input {
  width: 358px;
  margin: 0 36px;
  padding: 0;
  border-top: none;
  border-left: none;
  border-right: none;
  font-size: 14px;
  padding-bottom: 13px;
  border-bottom: 1px solid rgba(0, 0, 0, 0.2);
}

@media screen and (max-width: 544px) {
  .form__input {
    width: 238px;
    margin: 0 22px;
    padding-bottom: 9px;
  }
}

.form__input_type_invalid {
  border-bottom: 1px solid #ff0000;
}

.form__input-error {
  min-height: 30px;
  font-size: 12px;
  line-height: 1;
  margin: 5px 36px 0px;
  color: #f00;
}

@media screen and (max-width: 544px) {
  .form__input-error {
    min-height: 22px;
    margin: 5px 22px 0px;
  }
}

.form__input:focus {
  outline: none;
}

.form__save-button {
  width: 358px;
  padding: 0;
  height: 50px;
  margin: 18px 36px 36px 36px;
  border-top: none;
  border-left: none;
  border-right: none;
  background-color: #000;
  color: #fff;
  border-radius: 2px;
}

@media screen and (max-width: 544px) {
  .form__save-button {
    width: 238px;
    height: 46px;
    margin: 16px 22px 25px 22px;
    font-size: 14px;
  }
}

.form__save-button:hover {
  cursor: pointer;
  background-color: rgba(0, 0, 0, 0.8);
}

.form__save-button_disabled {
  background-color: transparent;
  border: solid 1px rgba(0, 0, 0, 0.2);
  color: rgba(0, 0, 0, 0.2);
}

.form__save-button_disabled:hover {
  background-color: transparent;
  border: solid 1px rgba(0, 0, 0, 0.2);
  color: rgba(0, 0, 0, 0.2);
  cursor: default;
}
